export default {
	data() {
		return {
			content:''
		}
	},
	onLoad() {
		
	},
	methods: {
		addFeedback() { // 反馈信息
			// 打开动画
			uni.showLoading({
				title: '提交中...',
				mask: true
			});
			uni.request({
				url: this.$serverUrl + '/api/feedback/addFeedback',
				method: 'POST',
				header:{
					'token':uni.getStorageSync('userinfo').token
				},
				data: {
					content: this.content,
					member_id:uni.getStorageSync('userinfo').member_id
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 关闭动画
						uni.hideLoading();
						uni.showToast({
							title: ret.data.message,
							duration: 2000,
							success:()=> {
								this.content = ''
							}
						});
					} 
					if (ret.data.code == 2) {
						// 气泡提示
						uni.showToast({
							title: '数据异常，请重新登录',
							duration: 2000,
							success() {
								uni.reLaunch({
									url: '../login/login'
								});
							}
						});
					} 
				}
			});
		}
	}
}
